.generator {
  width: 100%;
  margin: 0;
  padding: 0;
}

.generator__menu {
  display: flex;
  flex-direction: row;
  justify-content: center;
  box-sizing: border-box;
  margin: 0;
  padding: 10px;
  width: 100%;
  background-color: #006;
}

.generator__pic {
  width: 40px;
  height: 40px;
  margin: 0;
  margin-left: 10px;
}

.generator__buttonText {
  margin: 0;
  padding: 0;
}

@media screen and (max-width: 1000px) {
  .generator__pic {
    margin-left: calc(10px - (30px - 3vw));
    width: calc(40px - (10px - 1vw));
    height: calc(40px - (10px - 1vw));
  }
}

@media screen and (max-width: 640px) {
  .generator__buttonText {
    display: none;
  }

  .generator__buttonText_red {
    display: block;
  }

  .generator__pic {
    margin-left: 0;
    width: calc(60px - (64px - 10vw));
    height: calc(60px - (64px - 10vw));
  }
}