.help {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  align-items: center;
  width: 100%;
}

.help_hide {
  display: none;
}

.help__heading {
  display: flex;
  flex-direction: row;
  margin: 0;
  padding: 10px;
  align-items: center;
  width: calc(100% - 20px);
  max-width: 700px;
  justify-content: space-evenly;
  border-bottom: #006 3px double;
}

.help__title {
  margin: 0;
  padding: 10px;
  font-size: 24px;
  font-weight: 400;
}

.help__container {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin: 0;
  padding: 0;
}

.help__column {
  display: flex;
  flex-direction: column;
  max-width: 50%;
  margin: 0;
  padding: 10px;
  padding-bottom: 0;
}

.help__sector {
  margin: 0;
  padding: 0;
  border-bottom: #006 1px solid;
  display: flex;
  flex-direction: row;
}

.help__exaples {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
}

.help__pic {
  width: 40px;
  height: 40px;
  margin: 5px;
  padding: 0;
}

.help__description, .help__text{
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 5px 0;
  font-size: 16px;
  color: #425;
  text-align: left;
  font-family: 'Neucha', 'Arial Narrow', sans-serif;
}

.help__description_center {
  margin: 0 15px;
  border-bottom: #006 1px solid;
}

.help__description_center .help__text {
  text-align: center;
}

@media screen and (min-width: 1000px) {
  .help {
    padding-right: calc(50vw - 500px);
    padding-left: calc(50vw - 500px);
  }
}

@media screen and (max-width: 640px) {
  .help__container {
    flex-direction: column;
  }

  .help__column {
    max-width: 100%;
  }
}