.popup {
  margin: 0;
  padding: 0;
  width: 100%;
  box-sizing: border-box;
}

.popup_hide {
  display: none;
}

.popup__overlay {
  margin: 0;
  padding: 0;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  min-height: 100vh;
  background-color: rgba(0, 0, 6, .8);
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.popup_type_list .popup__overlay {
  position: absolute;
    background-color: rgba(0, 0, 6, .9);
}

.popup__inner {
  width: fit-content;
  display: flex;
  flex-direction: column;
  background-color: #006;
  margin: 0;
  padding: 0;
  border-radius: 10px;
  border: #ccc 1px solid;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.popup_type_list .popup__inner {

}


.popup__title {
  margin: 0;
  padding: 20px;
  width: 100%;
  box-sizing: border-box;
  letter-spacing: 1px;
  font-weight: 400;
  text-align: center;
  color: white;
  border-radius: 10px 10px 0 0;
}

.popup__text {
  font-family: 'Neucha', 'Arial Narrow', sans-serif;
  font-weight: 400;
  margin: 0;
  padding: 20px;
  color: white;
  text-align: center;
}

.popup__list {
  display: flex;
  flex-direction: column-reverse;
  flex-direction: column;
  width: 100%;
  max-width: 700px;
  margin: 0;
  padding: 0;
}

.popup__card {
  width: 100%;
  color: #425;
  font-family: 'Neucha', 'Arial Narrow', sans-serif;
  margin: 0;
  padding: 10px 30px 0;
  box-sizing: border-box;
  border-bottom: 1px #006 solid;
}

.popup__theme {
  margin: 0;
  padding: 0 0 10px;
  font-size: 20px;
}

.popup__container {
  margin: 0;
  padding: 0;
}

.popup__element {
  margin: 0;
  padding: 0;
  font-size: 16px;
}

.popup__element:last-of-type {
  padding-bottom: 10px;
}

.popup__element_hide {
  display: none;
}

.popup__workzone {
  box-sizing: border-box;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 0;
  padding: 20px;
}

.popup__button {
  width: 100px;
}

.popup__title .popup__button {
  width: fit-content;
  font-size: 16px;
}