
.header {
  margin: 0;
  padding: 0px 50px;
  box-sizing: border-box;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.header__top {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.header__profile {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: #600;
  width: 100px;
  border-radius: 0 0 50% 50%;
  height: fit-content;
  align-items: center;
}

.header__user {
  color: white;
  text-align: center;
}

.header__heading {
  margin: 0 0 0 20px;
  padding: 0;
  font-size: 40px;
  font-weight: 400;
  text-align: center;
  max-width: 600px;
  color: white;
}

.header__pic {
  width: 100px;
  height: 100px;
  margin: 0;
  padding: 0;
  border: #600 2px double;
  border-radius: 50%;
  animation: spin ease infinite 15s alternate;
}

.header__caution {
  background-color: rgba(255, 255, 255, 0.8);
  position: absolute;
  right: 25px;
  bottom: -40px;
  width: max-content;
  max-width: calc(100vw - 130px);
  display: none;
}

.header__language {
  line-height: 1;
  padding: 5px;
  margin: 0;
  border-radius: 8px;
  color: #600;
}

.header__language_active {
  background-color: rgba(102, 0, 0, 0.8);
  color: #fff;
}

@media screen and (max-width: 640px) {
  .header__heading {
    font-size: calc(40px - (32px - 5vw));
  }
}