@import url('../../vendor/normalize.css');
@import url('../../fonts/fonts.css');

.app {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  font-family: 'Rubik', 'Arial Bold', sans-serif;
  color: #600;
}

.main-background {
  background-color: #e5dbe2;
  background-image: url("data:image/svg+xml,%3Csvg width='64' height='64' viewBox='0 0 64 64' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8 16c4.418 0 8-3.582 8-8s-3.582-8-8-8-8 3.582-8 8 3.582 8 8 8zm0-2c3.314 0 6-2.686 6-6s-2.686-6-6-6-6 2.686-6 6 2.686 6 6 6zm33.414-6l5.95-5.95L45.95.636 40 6.586 34.05.636 32.636 2.05 38.586 8l-5.95 5.95 1.414 1.414L40 9.414l5.95 5.95 1.414-1.414L41.414 8zM40 48c4.418 0 8-3.582 8-8s-3.582-8-8-8-8 3.582-8 8 3.582 8 8 8zm0-2c3.314 0 6-2.686 6-6s-2.686-6-6-6-6 2.686-6 6 2.686 6 6 6zM9.414 40l5.95-5.95-1.414-1.414L8 38.586l-5.95-5.95L.636 34.05 6.586 40l-5.95 5.95 1.414 1.414L8 41.414l5.95 5.95 1.414-1.414L9.414 40z' fill='%23f2e9ff' fill-opacity='0.4' fill-rule='evenodd'/%3E%3C/svg%3E");
}

.custom-background {
  background-color: #220088;
  background-image: 
    repeating-linear-gradient(45deg, rgba(0,0,0,0.2) 2px, transparent 2.5px),
    repeating-linear-gradient(-45deg, rgba(0,0,0,0.2) 2px, transparent 2.5px),
    linear-gradient(to bottom, rgba(255,255,255,0.5) 1px, transparent 0), 
    linear-gradient(to bottom, rgba(255,255,255,0.5) 1px, transparent 0), 
    linear-gradient(to right, rgba(255,255,255,0.5) 1px, transparent 0), 
    linear-gradient(to right, rgba(255,255,255,0.5) 1px, transparent 0), 
    linear-gradient(to right, #660088 40px, transparent 0), 
    linear-gradient(to right, rgba(255,255,255,0.5) 2px, transparent 0);
  background-size:
    auto,
    auto,
    50px 50px, 
    50px 50px, 
    50px 50px, 
    50px 50px, 
    80px 80px, 
    50px 50px;
  background-position:
    0 0, 
    0 0,
    0 0, 
    0 10px, 
    0 0, 
    10px 0, 
    0 10px, 
    5px 0;
}

.main {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
 }

.hide {
  display: none;
}

.tools {
  margin: 0 auto auto;
  padding: 20px 50px;
  box-sizing: border-box;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  color: #425;
  font-family: 'Neucha', 'Arial Narrow', sans-serif;
}

.button {
  margin: 5px;
  padding: 7px 10px;
  border-radius: 10px;
  border: #425 3px double;
  font-family: 'Neucha', 'Arial Narrow', sans-serif;
  color: #425;
  background-color: rgba(255, 255, 255, 0.4);
  line-height: 1;
}

.button_gen {
  margin: 0 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 20px;
  color: #425;
  background-color: rgba(255, 255, 255, 0.4);
}

.button_gen_red {
  border: #600 4px double;
  background-color: rgba(255, 100, 128, 0.8);
  font-size: 30px;
  color: #600;
}

.button_switch {
  padding: 0;
  height: fit-content;
  position: relative;
}

.button:hover {
  background-color: rgba(255, 255, 255, 0.8);
}

.button_gen_red:hover {
  background-color: #600;
  color: #f58;
  border-color: #f58;
}

.button_switch:hover > .header__caution {
  display: none; 
}

.themes__button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-items: center;
}

.themes__text {
  margin: 0 20px 0 0;
  padding: 0;
  color: #425;
}

.bag {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}


@keyframes spin {
  from {
    transform: rotate(-60deg);
  }
  to {
    transform: rotate(60deg);
  }
}

@media screen and (min-width: 1400px) {
  .header, .main, .footer {
    padding-right: calc(50vw - 700px);
    padding-left: calc(50vw - 700px);
  }
}

@media screen and (max-width: 1000px) {
  .button_gen {
    margin: 10px calc(10px - (10px - 1vw));
    padding: calc(10px - (10px - 1vw));
    font-size: calc(20px - (10px - 1vw));
  }
  .button_gen_red {
    font-size: calc(30px - (20px - 2vw));
  }
}

@media screen and (max-width: 640px) {
 .header__heading {
  font-size: calc(40px - (640px - 100vw)/15);
 }
}

@media screen and (max-width: 575px) {
  .generate {
    line-height: 2;
  }
}

@media screen and (max-width: 500px) {
  .tools {
    max-width: 360px;
    flex-wrap: wrap;
    padding: 10px 20px;
  }
}