
.card {
  background-color: rgba(153, 119, 170, 0.8);
  margin: 10px;
  padding: 0;
  box-sizing: border-box;
  width: 280px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  border: rgba(68, 34, 85) 3px double;
  border-radius: 10px;
}

.card__header {
  background-color: rgba(153, 119, 170);
  margin: 0;
  padding: 10px;
  box-sizing: border-box;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.card__heading {
  margin: auto 0;
  padding: 0;
  font-weight: 400;
  font-size: 24px;
  line-height: 1;
  color: #fff;
}

.card__title {
  margin: 0;
  padding: 10px;
  font-size: 20px;
  line-height: 1;
  color: #536;
}

.card__title_hide {
  display: none;
}

.card__theme {
  margin: 0;
  padding: 10px;
  font-size: 24px;
  line-height: 1;
  font-family: 'Neucha', 'Arial Narrow', sans-serif;
}

.card__text {
  margin: 0;
  padding: 10px;
  font-size: 18px;
  line-height: 1;
  font-family: 'Neucha', 'Arial Narrow', sans-serif;
  color: #203;
}

@media screen and (max-width: 640px) {
  .card {
    width: 100%;
  }
}